import React, { useState, useContext } from 'react';
import { NavLink, Link, useHistory } from "react-router-dom";
import Nav from 'react-bootstrap/Nav'
import { Navbar, NavDropdown, Image, Form } from 'react-bootstrap/'
import logo from '../assets/csgone-t.png'
import hadoukensound from '../assets/hadouken.mp3'

var config = require('../config.json')

var Header = () => {


  const [themeChecked, setThemeChecked] = useState(false);

  function themeCheckbox()
  {
    var video = document.getElementById("twain")
  
    if (themeChecked === true) 
    {      
        video.muted = !video.muted
        setThemeChecked(false)
    } 
    if (themeChecked === false) 
    {
        video.muted = !video.muted
        setThemeChecked(true)
    }    
  }

  const [themeStopped, setThemeStopped] = useState(false);

  function themeStopbox()
  {
    var video = document.getElementById("twain")
  
    if (themeStopped === true) 
    {      
        video.play()
        setThemeStopped(false)
    } 
    if (themeStopped === false) 
    {
        video.pause()
        setThemeStopped(true)
    }    
  }


  function setVolume(volume)
  {
      var video = document.getElementById("twain")
      
      if (video.muted) {
        video.muted = !video.muted
      }
      video.volume = volume 
    
  }

  var hadoukenMP3 = new Audio(hadoukensound)

  return (
    <>
    <Navbar collapseOnSelect defaultExpanded expand="sm" >
    <Navbar.Brand> <img src={logo} alt="logo" width="40" height="40" className="d-inline-block align-top"/></Navbar.Brand>

    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
    <Navbar.Collapse id="responsive-navbar-nav">
      <Nav className="mr-auto">
        <NavLink to="/all-time" activeClassName="selected" id="alltime">all time</NavLink>       
        <NavLink to="/weekly" activeClassName="selected" id="weekly">weekly</NavLink>
        <NavLink to="/individual" activeClassName="selected" id="individual">individual</NavLink>
      </Nav>
        <Nav>     
             <div id="volumeicon">🔊</div>    
            <Form.Control custom id="volume" type="range" min="0" max="1" step="0.01" defaultValue="0" onChange={(e) => setVolume(e.target.value)}/>
         </Nav>

        <Nav>
          &nbsp;
        <Form.Check     
        checked={themeStopped}
        type="switch"
        label="⏸"     
        onClick={() => themeStopbox()} 
        id="stopVideo" 
        name="stopVideo" 
      />    
      </Nav>
      {/*
      <Nav>
      <Form.Check     
        checked={themeChecked}
        type="switch"
        label="🔊"     
        onClick={() => themeCheckbox()} 
        id="muteVideo" 
        name="muteVideo" 
      />    
      </Nav>
      */}
    </Navbar.Collapse>
  </Navbar>
  <br></br>
  </>

  );
}

export default Header;