import React, { useState, useEffect, useCallback } from 'react';
import '../css/style.css';
import mvp from '../assets/Scoreboard_mvp.webp'
import { Button, Card, CardDeck, Container, Col, Row, Form, Image, Jumbotron } from 'react-bootstrap';
import { LabelList, BarChart, Cell, RadarChart, PieChart, Pie, ResponsiveContainer, Label, Treemap, RadialBarChart, RadialBar, PolarGrid, PolarAngleAxis, PolarRadiusAxis, Radar, Bar, YAxis, Legend, Text, LineChart, XAxis, Tooltip, CartesianGrid, Line } from 'recharts'
const weeklyjson = require('../data/weekly-ind.json')


var Weekly = () => {

  const [kentoData, setKentoData] = useState([{}]); 
  const [currentWeek, setCurrentWeek] = useState("24_07_2020"); 

  var week = {
    week: "2020_07_24"
  }

  useEffect(() => {
    fetch("https://safe-thicket-77515.herokuapp.com/api/rankme-week", {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(week)
  })
    .then(res => res.json())
    .then (
      (result) => {
        setKentoData(result);
      },
      (error) =>{
        setKentoData(error);
      }
    );
    
  }, [])

const changeWeek = (newWeek) => {

  var weekTo = {
    week: newWeek
  }
  
  fetch("https://safe-thicket-77515.herokuapp.com/api/rankme-week", {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(weekTo)
  })
    .then(res => res.json())    
    .then (
      (result) => {
        setKentoData(result);
      },
      (error) =>{
        setKentoData(error);
      }
    )
}

/* 
  const week1 = weeklyjson.filter((week) => { return week.date === "2020-07-24" })
  const week2 = weeklyjson.filter((week) => { return week.date === "2020-07-31" })
  const week3 = weeklyjson.filter((week) => { return week.date === "2020-08-07" })

  const [kentoData, setKentoData] = useState(week1); 
*/

 // add new kv pairs to json
  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["accuracy"]= +kentoData[object].hits / +kentoData[object].shots * 100
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["kd"]= +kentoData[object].kills / +kentoData[object].deaths
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["kda"]= (+kentoData[object].kills +  +kentoData[object].assists) / +kentoData[object].deaths
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["sumOtherHits"]= +kentoData[object].left_arm + +kentoData[object].left_leg + +kentoData[object].right_arm + +kentoData[object].right_leg
  });

  var sortShots = (a, b) => {
    return b.shots-a.shots
  };
 
  var sortKills = (a, b) => {
    return b.kills-a.kills
  };

  var sortHeatmap = (a, b) => {
    return b.hits-a.hits
  };
  
  var sortAccuracy = (a, b) => {
    if (a.accuracy < b.accuracy){return 1;}
    if (a.accuracy > b.accuracy){return -1;}
    return 0;
  };

  var sortMvp = (a, b) => {
    return b.mvp-a.mvp
  };

  var sortKd = (a, b) => {
    return b.kd-a.kd
  };

  var sortKda = (a, b) => {
    return b.kda-a.kda
  };

  var sortAllWeapons = (a, b) => {
    return b.kills-a.kills
  };

const killData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortKills)
]

const shotData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortShots)
]

const heatmapData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortHeatmap)
]

const accuracyData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortAccuracy)
]

const mvpData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortMvp)
]

const kdData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortKd)
]

const kdaData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortKda)
]

const weaponData = [
  ...kentoData.filter((data) => { return data.kills != "0" }).sort(sortAllWeapons)
]



var sumKnife = weaponData.map(weapon => weapon.knife).reduce((a, b) => +a + +b);
var sumGlock = weaponData.map(weapon => weapon.glock).reduce((a, b) => +a + +b);
var sumUspSilencer = weaponData.map(weapon => weapon.usp_silencer).reduce((a, b) => +a + +b);
var sumP250 = weaponData.map(weapon => weapon.p250).reduce((a, b) => +a + +b);
var sumDeagle = weaponData.map(weapon => weapon.deagle).reduce((a, b) => +a + +b);
var sumElite = weaponData.map(weapon => weapon.elite).reduce((a, b) => +a + +b);
var sumHkp2000 = weaponData.map(weapon => weapon.hkp2000).reduce((a, b) => +a + +b);
var sumFiveSeven = weaponData.map(weapon => weapon.fiveseven).reduce((a, b) => +a + +b);
var sumTec9 = weaponData.map(weapon => weapon.tec9).reduce((a, b) => +a + +b);
var sumCz75a = weaponData.map(weapon => weapon.cz75a).reduce((a, b) => +a + +b);
var sumRevolver = weaponData.map(weapon => weapon.revolver).reduce((a, b) => +a + +b);
var sumNova = weaponData.map(weapon => weapon.nova).reduce((a, b) => +a + +b);
var sumXm1014 = weaponData.map(weapon => weapon.xm1014).reduce((a, b) => +a + +b);
var sumMag7 = weaponData.map(weapon => weapon.mag7).reduce((a, b) => +a + +b);
var sumSawedoff = weaponData.map(weapon => weapon.sawedoff).reduce((a, b) => +a + +b);
var sumM249 = weaponData.map(weapon => weapon.m249).reduce((a, b) => +a + +b);
var sumNegev = weaponData.map(weapon => weapon.negev).reduce((a, b) => +a + +b);
var sumMac10 = weaponData.map(weapon => weapon.mac10).reduce((a, b) => +a + +b);
var sumMp9 = weaponData.map(weapon => weapon.mp9).reduce((a, b) => +a + +b);
var sumMp7 = weaponData.map(weapon => weapon.mp7).reduce((a, b) => +a + +b);
var sumUmp45 = weaponData.map(weapon => weapon.ump45).reduce((a, b) => +a + +b);
var sumP90 = weaponData.map(weapon => weapon.p90).reduce((a, b) => +a + +b);
var sumBizon = weaponData.map(weapon => weapon.bizon).reduce((a, b) => +a + +b);
var sumGalilar = weaponData.map(weapon => weapon.galilar).reduce((a, b) => +a + +b);
var sumAk47 = weaponData.map(weapon => weapon.ak47).reduce((a, b) => +a + +b);
var sumScar20 = weaponData.map(weapon => weapon.scar20).reduce((a, b) => +a + +b);
var sumUmp45 = weaponData.map(weapon => weapon.ump45).reduce((a, b) => +a + +b);
var sumM4a1 = weaponData.map(weapon => weapon.m4a1).reduce((a, b) => +a + +b);
var sumM4a1Silencer = weaponData.map(weapon => weapon.m4a1_silencer).reduce((a, b) => +a + +b);
var sumAug = weaponData.map(weapon => weapon.aug).reduce((a, b) => +a + +b);
var sumSsg08 = weaponData.map(weapon => weapon.ssg08).reduce((a, b) => +a + +b);
var sumAwp = weaponData.map(weapon => weapon.awp).reduce((a, b) => +a + +b);
var sumG3sg1 = weaponData.map(weapon => weapon.g3sg1).reduce((a, b) => +a + +b);
var sumSg556 = weaponData.map(weapon => weapon.sg556).reduce((a, b) => +a + +b);

var allWeapons = [
  { weapon: 'Knife', kills: sumKnife },
  { weapon: 'Glock', kills: sumGlock },
  { weapon: 'usp_silencer', kills: sumUspSilencer },
  { weapon: 'P250', kills: sumP250 },
  { weapon: 'deagle', kills: sumDeagle },
  { weapon: 'elite', kills: sumElite },
  { weapon: 'hkp2000', kills: sumHkp2000 },
  { weapon: 'fiveseven', kills: sumFiveSeven },
  { weapon: 'tec9', kills: sumTec9 },
  { weapon: 'cz75a', kills: sumCz75a },
  { weapon: 'revolver', kills: sumRevolver },
  { weapon: 'nova', kills: sumNova },
  { weapon: 'xm1014', kills: sumXm1014 },
  { weapon: 'mag7', kills: sumMag7 },
  { weapon: 'sawedoff', kills: sumSawedoff },
  { weapon: 'm249', kills: sumM249 },  
  { weapon: 'negev', kills: sumNegev },   
  { weapon: 'mac10', kills: sumMac10 },
  { weapon: 'mp9', kills: sumMp9 },
  { weapon: 'mp7', kills: sumMp7 },
  { weapon: 'ump45', kills: sumUmp45 },
  { weapon: 'p90', kills: sumP90 },    
  { weapon: 'bizon', kills: sumBizon },  
  { weapon: 'galilar', kills: sumGalilar },
  { weapon: 'ak47', kills: sumAk47 },
  { weapon: 'scar20', kills: sumScar20 },
  { weapon: 'famas', kills: sumUmp45 },
  { weapon: 'm4a1', kills: sumM4a1 },    
  { weapon: 'm4a1_silencer', kills: sumM4a1Silencer },  
  { weapon: 'aug', kills: sumAug },  
  { weapon: 'ssg08', kills: sumSsg08 },  
  { weapon: 'awp', kills: sumAwp },  
  { weapon: 'g3sg1', kills: sumG3sg1 },      
  { weapon: 'SG 553', kills: sumSg556 },  
];






  
 
    return (
      <>
 
 <Container>    


<Jumbotron>
  <div id="tagline"></div>
    <h1>Weekly stats - {currentWeek}</h1>
     <button class="button1" onClick={() => {setCurrentWeek("24/07/2020"); changeWeek("2020_07_24")}}>24/07/20</button>
     <button class="button1" onClick={() => {setCurrentWeek("31/07/2020"); changeWeek("2020_07_31")}}>31/07/20</button> 
     <button class="button1" onClick={() => {setCurrentWeek("07/08/2020"); changeWeek("2020_08_07")}}>07/08/20</button> 
    
  
</Jumbotron>
<Jumbotron>
     <ResponsiveContainer height={450}>
      <BarChart     
        data={killData}
       height={400}
        stackOffset="expand"  >
<CartesianGrid strokeDasharray="3 3" />
       <XAxis dataKey="name" xAxisId="0" tick={{fontSize: "10px",}} interval={0}/>
       <XAxis  dataKey="name" xAxisId="1" tick={false}/>        
       <YAxis domain={[0, 150]}/>
        <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }} />   
        <Bar dataKey="kills" xAxisId="1" fill="#C7007A" /> 
        <Bar dataKey="headshots"  xAxisId="0" fill="#8884d8" />  
        <Legend wrapperStyle={{bottom: 25, left: 25}}/>
        </BarChart>
        </ResponsiveContainer>

      <ResponsiveContainer height={450}>
        <BarChart height={450} data={shotData}
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="name" xAxisId="0" tick={{fontSize: "10px",}} interval={0}/>
       <XAxis  dataKey="name" xAxisId="1" tick={false}/>
       <YAxis  domain={[0, 3000]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 25, left: 25}}/>
       <Bar dataKey="shots" fill="#82ca9d" xAxisId="1" /> 
       <Bar dataKey="hits" fill="#8884d8" xAxisId="0" />       
      </BarChart>
      </ResponsiveContainer>

      <ResponsiveContainer height={300}>
      <BarChart data={accuracyData} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="name" tick={{fontSize: "10px",}} interval={0}/>
       <YAxis  domain={[0, 30]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 10, left: 25}}/>
       <Bar dataKey="accuracy" fill="#82ca9d" name="accuracy (%)" /> 
     
      </BarChart>
      </ResponsiveContainer>

      
      <ResponsiveContainer height={450}>
      <BarChart data={heatmapData} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="name" tick={{fontSize: "10px",}} interval={0}/>       
       <YAxis  domain={[0, 750]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 15, left: 25}}/>
       <Bar dataKey="stomach" stackId="a" fill="#C7007A" />      
       <Bar dataKey="chest" stackId="a" fill="#ebe53f" />     
       <Bar dataKey="sumOtherHits" stackId="a" fill="#82ca9d" name="other"/> 
       <Bar dataKey="head" stackId="a" fill="#8884d8" />       
      </BarChart>
      </ResponsiveContainer>

      <ResponsiveContainer height={300}>
      <BarChart data={mvpData} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="name" tick={{fontSize: "10px",}} interval={0}/>
       <YAxis  domain={[0, 45]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 10, left: 25}} iconType="star"/>
       <Bar dataKey="mvp" fill="#32a4a8" name="mvp" /> 
     
      </BarChart>
      </ResponsiveContainer>

      <ResponsiveContainer height={300}>
      <BarChart data={kdData} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="name" tick={{fontSize: "10px",}} interval={0}/>
       <YAxis  domain={[0, 5]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 10, left: 25}} />
       <Bar dataKey="kd" fill="#327fa8" name="kd ratio" /> 
     
      </BarChart>
      </ResponsiveContainer>
      
      <ResponsiveContainer height={300}>
      <BarChart data={kdaData} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="name" tick={{fontSize: "10px",}} interval={0}/>
       <YAxis  domain={[0, 5]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 10, left: 25}} />
       <Bar dataKey="kda" fill="#3253a8" name="kda ratio" /> 
     
      </BarChart>
      </ResponsiveContainer>

      <ResponsiveContainer height={300}>
      <BarChart data={allWeapons.sort(sortAllWeapons)} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="weapon" tick={{fontSize: "10px", textAnchor: "end", angle: "-30"}} interval={0}/>
       <YAxis  domain={[0, 200]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 0, left: 25}} />
       <Bar dataKey="kills" fill="#4c32a8" name="kills by weapon" /> 
     
      </BarChart>
      </ResponsiveContainer>
 
      </Jumbotron>
</Container>

</>

  )
}

  
export default Weekly;