function setVolume(volume)
{
    var video = document.getElementById("twain")
    
    if (video.muted) {
      video.muted = !video.muted
    }
    video.volume = volume 
  
}

    

var Container = ({location}) => {


const upPress = useKeyPress('ArrowUp');
const downPress = useKeyPress('ArrowDown');
const leftPress = useKeyPress('ArrowLeft');
const rightPress = useKeyPress('ArrowRight');
const bPress = useKeyPress('b');
const aPress = useKeyPress('a');

const iPress = useKeyPress('i');
const dPress = useKeyPress('d');
const qPress = useKeyPress('q');


    return (        
        <div>
{combo === 'ArrowUpArrowUpArrowDownArrowDownArrowLeftArrowRightArrowLeftArrowRightba' && change()}
{combo === 'iddqd' && changeDoom()}
               
             <video autoPlay="autoplay" loop="loop" muted className={classes.Video} id="twain" src={stdimm}>               
            </video>
           
            <TransitionGroup className="transition-group">
                <CSSTransition key={location.key} timeout={{enter: 150, exit: 0}} classNames={'fade'}>
                    <section className="route-section">
                        <Switch key={location.key} location={location}>                
                            <Route path="/all-time" component={AllTime} />
                            <Route path="/individual" component={Individual} />
                            <Route path="/weekly" component={Weekly} />
                            <Route path="/">
                                <AllTime />           
                            </Route>                            
                        </Switch>
                    </section>
                    </CSSTransition>
            </TransitionGroup>
        </div>  
    );
}


export default withRouter(Container);